:root {
  --background: #ffffff;
  --foreground: #106100;
}
body {
  background: var(--background);
  color: var(--foreground);
  max-width: 100%;
  min-height: 100vh;
}
#modal {
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.192);
  position: fixed;
  top: 0;
  align-items: center;
  display: flex;
  justify-content: center;
  backdrop-filter: blur(10px);
  left: 0;
  right: 0;
  bottom: 0;
}
h1 {
  font-size: 54px;
}
h2 {
  font-size: 35px;
}
h3 {
  font-size: 25px;
}
.thunder {
  clip-path: polygon(66% 0, 47% 46%, 66% 46%, 41% 96%, 55% 55%, 38% 52%);
}
.thunder-container {
  filter: drop-shadow(0 0 10px green);
}
.thunder-item {
  width: 100%;
  filter: drop-shadow(0 0 10px green);
  position: absolute;
  background-color: #1f9e05;
  animation: downThunder 5s linear infinite;
}
@keyframes downThunder {
  0% {
    height: 0px;
  }
  50% {
    height: 100%;
  }
  100% {
    height: 0px;
  }
}
